function renderSingleRandomEvent() {
    const container = document.getElementById('random-event-container');
    if (!container) return;
 
    const game = rawData[Math.floor(Math.random() * rawData.length)];
 
    const dateFormatted = new Date(game.CreatedAt).toLocaleDateString('fr-FR', {
        day: 'numeric', 
        month: 'long', 
        year: 'numeric'
    });
 
    const fields = [
        { label: "Jeu", value: game.Jeu },
        { label: "Joueurs", value: (game["Joueurs mini"] && game["Joueurs maxi"]) ? `${game["Joueurs mini"]} - ${game["Joueurs maxi"]}` : "Non spécifié" },
        { label: "Temps de jeu", value: game["Temps de jeu"] || "Non spécifié" },
        { label: "Type", value: game["Coopération / Compétition"] || "Non spécifié" },
{ label: "Âge minimim", value: game["Âge mini"] || "Non spécifié" },
{ label: "Temps de 1ère mise en place (en min)", value: game["Temps de 1ère mise en place (en min)"] || "Non spécifié" },
        { label: "Ajouté le", value: dateFormatted }
    ];
 
    container.textContent = '';
 
    fields.forEach(field => {
        const line = document.createElement('div');
        line.className = 'event-info-line';
 
        const labelSpan = document.createElement('span');
        labelSpan.className = 'label';
        labelSpan.textContent = field.label + " :";
 
        const valueSpan = document.createElement('span');
        valueSpan.textContent = " " + field.value;
 
        line.appendChild(labelSpan);
        line.appendChild(valueSpan);
        container.appendChild(line);
    });
}
 
document.addEventListener('DOMContentLoaded', () => {
    const btn = document.getElementById('btn-random-event');
    if (btn) {
        btn.addEventListener('click', renderSingleRandomEvent);
    }
});
